var staticRenderFns = [function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"my-div"},[_c('p',[_vm._v("  无论是修补世界法则还是献身世界树中辅助法则运行，似乎都不能让这个世界永远和平地运行下去，计算机大陆将永远陷入危机的轮回之中。 "),_c('br'),_vm._v("   很久以前，这个世界由一个神所创造，在创世神的庇护下，计算机大陆的人们安宁地生活着。然而，某个时刻，计算机大陆的人们决定建造一座巨塔，在巨塔中存放整个大陆的知识与无尽的魔力；巨塔高耸入云，人们妄图爬上巨塔看看神明与星空之上的世界。人类这种傲慢的行为最终惹恼了神，神对计算机大陆的人类降下了神罚，自己离开了这个世界。辉煌的古文明建造的巨塔也不再被称为巨塔，而是因其外观如同一棵树又处于世界中心，因此叫作世界树。 "),_c('br'),_vm._v("   一开始，离开了神，世界依然能够正常运行，人类仅仅是接受了神罚，语言不再相通，狂妄却聪慧的人类随着时间推移依然重建了文明。然而，在发达的文明世界背后，危机悄然而至。世界在运行中必须不断面临内部和外部的冲击，世界法则会因冲击而磨损、失衡，最终崩坏，导致世界在混乱中毁灭。在创世神的保护下，世界法则磨损后也能够得到修复。然而，神离开这个世界后，世界法则的磨损不断累积，到了濒临崩坏的边缘。初代大贤者魔力震古烁今，他集结整个世界的魔力，将世界法则在世界树精神空间中显形，变为人类可以理解的形式；他牺牲了自己的躯体，将灵魂献祭于世界树中，创立了大贤者魔力与记忆的传承。然而，即使是如此浩大的工作也仅仅是让这个世界脆弱地生存着，每次产生危机都有世界毁灭的风险。这悲哀的轮回该如何打破？ "),_c('br'),_vm._v("   你相信你已经找到了答案。人的力量是有限的，强大如初代大贤者也不能永远支撑世界运行，灵魂力量随着时间推移逐渐变弱，不得不找人继任。一次的修补世界法则也只能救一时之急，法则的磨损不可能避免，不继续传承贤者之力只会面临更绝望的未来。——想要拯救这个世界，只有成为神明。 "),_c('br'),_vm._v("   神明与凡人的界限在何处？是信仰。昔日之创世神，真的离开了这个世界吗？当人们建成巨塔之时，心中早已失去了对于神明的敬畏与信仰，创世神随着信仰的消失一起消失了。传说中，神罚让人们讲不同的语言。但真相难道不是自私的人类为了自己的利益与秘密制造了自己的语言，抛弃了共通的语言吗？更进一步，创世神真的创造了这个世界吗？又何尝不是世界中的人们相信这个世界是由一个神所创造，于是创世神诞生了呢？ "),_c('br'),_vm._v("   千秋万代之和平，愿毕其功于一役。与其让世界在毁灭的边缘挣扎，还不如就此一搏！世界树内无尽的魔力裹挟着你的愿望传递给了全世界。你只能心如止水，在世界树的灵魂空间内静静等待一切的变化。 "),_c('br'),_vm._v("   在世界的危机面前，每个人都渺小的如同蝼蚁。前所未有的危机将说着不同语言、拥有不同财富、抱有不同想法的人们凝聚在一起，共同祈祷世界的永远存在。没有了神明，位于世界中心巍然矗立的世界树就是人们信仰的对象：这棵树见证了人类的兴盛与危难、和平与动荡，在整个世界生死存亡的此刻，人们衷心祈祷这棵古老的世界树能够拯救一切。 "),_c('br'),_vm._v("   前所未有的危机凝聚的信仰力量远远超过了你的想象，浩大的信仰之力在世界树内激荡着。然而，神力需要有一个寄托的对象，但世界树归根结底仍然是一个人造物，本来没有灵魂，无法成神，但世界树又是无数人们信仰的对象。你下定了决心：如果世界树没有灵魂，自己就是世界树的灵魂。无形的信仰之力凝为实体，灌注入你渺小的灵魂——你的灵魂逐渐神化，属于人类的杂质被抛弃，灵魂之火不断变大、变得透明...... "),_c('br'),_vm._v("   无数年后，这么一则古老的传说将会不断流传下去：面对世界毁灭的危机，世界树诞生了树灵，拯救了人类，并将守护人类，直至永远。 ")])])
