import Vue from 'vue'
import VueRouter from 'vue-router'
import Web0 from '../components/Web0.vue'
import FinalView from '../components/FinalView.vue'
import Final1View from '../components/Final1View.vue'
import Final2View from '../components/Final2View.vue'
import Final3View from '../components/Final3View.vue'

Vue.use(VueRouter)

const routes = [
  { path: '/', component: Web0 },
  { path: '/final', component: FinalView },
  { path: '/final1', component: Final1View },
  { path: '/final2', component: Final2View },
  { path: '/final3', component: Final3View }
]

const router = new VueRouter({
  routes
})

export default router
