<template>
  <div class="my-div">
    <p>
      &emsp;&emsp;经过了艰苦的探索，你终于打开了通往世界树深层的机关。一道闪着光的阶梯从面前出现，穿过地面向下通向世界树的树根深处。你犹豫片刻，踏上了阶梯，一步一步向地下走去，每走一步，走过的阶梯便在身后崩解成光的碎片，跟随在你的身后。阶梯穿过地面时，你的脚步竟然没有受到阻挡，继续走了下去。走着走着，世界树盘绕虬结的树根越来越多，却碰不到实体，从你的身体中直接穿过。光的阶梯崩解出的碎片越来越多，本该幽暗无光的地底也闪烁出了一星一点的光芒，随后是几个坚定的光点，随后光点越来越多，如漫天星空般璀璨夺目。最终，阶梯终于走到了尽头，无数光的碎片飞舞着，盘旋着，逐渐凝聚成一个门的形状。门上没有把手，却有一个显示屏，屏上一个输入框等待着字符；显示屏前，光点们汇集成了一个悬在空中的键盘，明暗闪烁着，似乎催促着你用它敲击出宇宙的真理。
      <br>

    </p>
    <p class="center">
      &emsp;&emsp;请决定你要输入什么吧！
      <br>
      <br>
      （注意：你输入的内容将决定剧情的走向和结局！）
    </p>
    <el-input @input="handleinput" class='custom-input' v-model="input" placeholder="请输入内容"></el-input>
    <el-button @click="handleclick">解锁</el-button>
  </div>
</template>

<script>
// @ is an alias to /src

export default {
  data () {
    return {
      input: '',
      wrong_times: 0
    }
  },
  methods: {
    handleinput (value) {
      console.log(value)
    },
    handleclick () {
      if (this.input === 'restore') {
        this.$router.push('/final1')
      } else if (this.input === 'guard') {
        this.$router.push('/final2')
      } else if (this.input === 'deify') {
        this.$router.push('/final3')
      } else {
        this.wrong_times++
        if (this.wrong_times >= 3) {
          this.$message({
            duration: 800,
            showClose: true,
            message: '初始传单，暗藏玄机',
            type: 'error'
          })
        } else {
          this.$message({
            duration: 800,
            showClose: true,
            message: '你真的做好打开真理之门的准备了吗？',
            type: 'error'
          })
        }
      }
    }
  }
}
</script>

