  },
  name: 'HelloWorld',
  data () {
    return {
      option: null,
      showText: true
    }
  },
  methods: {
    check_answer () {
      const answer = [1, 2, 5, 6, 7, 9, 12]
      const result = []
      for (let i = 0; i < 13; i++) {
        if (this.option.series[0].data[i].itemStyle.color === 'red') {
          result.push(this.option.series[0].data[i].name - '0')
        }
      }
      result.sort((a, b) => a - b)
      console.log(result)
      if (result.length === answer.length) {
        for (let j = 0; j < answer.length; j++) {
          if (result[j] !== answer[j]) {
            return false
          }
        }
        console.log('success')
        return true
      } else {
        return false
      }
    },
    initChart () {
      // 初始化echarts
      const myChart = echarts.init(document.getElementById('chart'))
      // myChart.resize() // 自适应大小
      this.option = this.setOption()
      myChart.setOption(this.option)

      myChart.on('click', (params) => {
        console.log(params)
        if (!params.data.itemStyle) {
          params.data.itemStyle = {}
        }
        if (params.data.itemStyle.color === 'black') {
          this.option.series[0].data[params.dataIndex].itemStyle.color = 'red'
        } else {
          this.option.series[0].data[params.dataIndex].itemStyle.color = 'black'
        }
        myChart.setOption(this.option, false, false)
        if (this.check_answer()) {
          this.$router.push('/final')
        }
      })
    },
